.imageUpload {
  position: relative;
  background: url(../../images/base/dfImg.png) no-repeat center center;
  background-size: cover;
  width: 150px;
  height: 150px;
  overflow: hidden; }
  .imageUpload.rectangle {
    width: 150px;
    height: 170px; }
  .imageUpload .show-image,
  .imageUpload .choose-image[type="file"] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .imageUpload .choose-image[type="file"] {
    opacity: 0;
    z-index: 20; }
  .imageUpload .show-image {
    z-index: 10; }
  .imageUpload .edit-delete,
  .imageUpload .edit-zoom {
    position: absolute;
    z-index: 99;
    display: inline-block;
    padding: 5px; }
  .imageUpload .edit-delete {
    right: 50px; }
  .imageUpload .edit-zoom {
    right: 10px; }

